	module.exports = {"data":{"site":{"siteMetadata":{"title":"Glen Cheney"}},"markdownRemark":{"html":"<p>A company directory app built with React + Redux for Odopod showing employee information and a seating map.</p>\n<h2>Where does Tim sit?</h2>\n<p>The directory helped Odopod employees find where their coworkers sit with the seating map and gave profile pages to each person. Odopeeps could add their contact information like AIM screen names (in a time before Slack ðŸ˜±), Twitter, and Instagram.</p>\n<p>The we.odopod directory was one of the first projects I worked on in 2012 at Odopod. At that time, we built it on top of Twitter Bootstrap, it used <a href=\"https://github.com/defunkt/jquery-pjax\">jQuery Pjax</a> for partial page loads, and used Django for a backend and CMS. I built the original seating map with custom zooming (CSS transforms), panning, and hotspots.</p>\n<h2>A single resource</h2>\n<p>Fast-forward to 2017. A couple of Odopod designers have some idle time, so they begin to work on a redesign for we.odopod which would include more features and consolidate information from multiple other places into this single resource.</p>\n<p>The final feature set for we.odopod was a directory listing of all the employees (and their pets), a detail page for each person, the seating map, a career path section of the site which described different roles at Odopod, and a resources section which was mostly migrated HR content.</p>\n<p>This was also a great opportunity for a couple of the Odopod developers to get some experience with React because it was an internal project and a single page application.</p>\n<h2>New hotness</h2>\n<p>We started development of the new site using <a href=\"https://github.com/facebook/create-react-app\">Create React App</a>. CRA gave us a great foundation to start development. We also wanted to make the app backend agnostic in case we decided to change that too. The new we.odopod app consumes RESTful APIs from the Django backend for authentication, employee information, resource pages, and more.</p>\n<p>The appâ€™s navigation is handled by <a href=\"https://www.npmjs.com/package/react-router\"><code class=\"language-text\">react-router</code></a>, <a href=\"https://www.npmjs.com/package/react-loadable\"><code class=\"language-text\">react-loadable</code></a> dynamically loads components only when theyâ€™re needed, and <a href=\"https://www.npmjs.com/package/leaflet\">Leaflet.js</a> powers the map. We use the <a href=\"https://www.npmjs.com/package/@odopod/odo-sassplate\"><code class=\"language-text\">@odopod/odo-sassplate</code></a> library for base and common styles. <a href=\"/shuffle/\">My Shuffle library</a> is used on the home page to filter, sort, and search people.</p>\n<p>Being new to React, I didnâ€™t immediately push us to use Redux (or any other state manager). After we completed the project, however, I had some idle time and decided to use it learning and implementing Redux into our app.</p>\n<!-- markdownlint-disable MD033 -->\n<video muted playsinline controls loop poster=\"/we-dot-odopod-poster.png\">\n  <source src=\"/static/we-dot-odopod-abb465b767610c79621b653d0058b061.webm\" type=\"video/webm; codecs=vp9,vorbis\">\n  <source src=\"/static/we-dot-odopod-bf48ad5e565e948af9daced844937c5d.mp4\" type=\"video/mp4\">\n</video>","frontmatter":{"title":"we.odopod","imageDescription":"Screenshot of the profile page for an employee.","href":"https://we.odopod.com/","tags":["React","Redux"]}},"hero":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.777434312210201,"src":"/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-0fe8f.png","srcSet":"/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-d7863.png 350w,\n/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-c146b.png 700w,\n/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-0fe8f.png 1400w,\n/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-25199.png 2100w,\n/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-9880c.png 2300w","srcWebp":"/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-43730.webp","srcSetWebp":"/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-1130a.webp 350w,\n/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-7944d.webp 700w,\n/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-43730.webp 1400w,\n/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-beeb1.webp 2100w,\n/static/we-dot-odopod-hero-80a160b85804ea0dbcb75ee1f2c361f2-71e2f.webp 2300w","sizes":"(max-width: 1400px) 100vw, 1400px"}}},"pathContext":{"slug":"/we-dot-odopod/","heroImage":"/images/we-dot-odopod-hero.png/","nextProject":{"frontmatter":{"title":"Audemars Piguet"},"fields":{"slug":"/audemars-piguet/"}}}}

